.button {
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: #81b3d2;
  color: #ffffff;
  line-height: 26px;
  font-size: 21px;
  font-weight: 700;
  border: none;
  text-align: center;
  padding-top: 18px;
  padding-bottom: 14px;
  cursor: pointer;

  &:hover {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;
  }

  &--search {
    width: 284px;
    padding-top: 16px;
    padding-bottom: 16px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-decoration: none;

    @include tablet {
      width: 458px;
      padding-top: 14px;
      padding-bottom: 17px;
    }
  }
}
